import { AgGridVue } from 'ag-grid-vue3';
import axios from 'axios';
import Swal from 'sweetalert2';
export default {
  components: {
    AgGridVue,
    Swal
  },
  data() {
    return {
      rowData: [{
        emp_code: "",
        department: "",
        hire_date: "",
        tel: "",
        pwd: ''
      }],
      columnDefs: [{
        field: 'emp_code',
        headerName: '사원번호',
        flex: 1
      }, {
        field: 'emp_name',
        headerName: '이름',
        flex: 1
      }, {
        field: 'department',
        headerName: '부서',
        flex: 1,
        valueFormatter: params => {
          if (params.value == 'DP1') {
            return params.value = '관리자';
          } else if (params.value == 'DP2') {
            return params.value = '영업';
          } else if (params.value == 'DP3') {
            return params.value = '생산';
          } else if (params.value == 'DP4') {
            return params.value = '품질';
          } else if (params.value == 'DP5') {
            return params.value = '자재';
          } else if (params.value == 'DP6') {
            return params.value = '설비';
          }
        }
      }, {
        field: 'hire_date',
        headerName: '입사일',
        flex: 1
      }, {
        field: 'tel',
        headerName: '전화번호',
        flex: 1
      }],
      gridOptions: {
        pagination: true,
        paginationPageSize: 10,
        paginationPageSizeSelector: [5, 10, 20, 50],
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          //컬럼 드래그로 순서바꾸기 못하게
          resizable: false,
          //컬럼 너비 마우스로 조절 못하게
          sortable: false,
          //정렬 기능 비활성화
          onGridReady: function (event) {
            event.api.sizeColumnsToFit();
          }
        }
      },
      selectedSecondIndex: null,
      //상세그리드 행 인덱스
      prodIndex: null,
      info: {
        emp_code: "",
        emp_name: "",
        department: "",
        hire_date: "",
        tel: "",
        pwd: ""
      },
      departmentListAry: [],
      selectedSecondIndex: null,
      prodIndex: null
    };
  },
  async mounted() {
    await this.EmployeesData();
    await this.departmentList();
  },
  methods: {
    async EmployeesData() {
      try {
        const response = await axios.get('/api/admin/employees');
        this.rowData = response.data;
      } catch (err) {
        console.error('데이터 조회 실패:', err);
      }
    },
    empCellClicked(event) {
      let emp = event.data.emp_code;
      this.prodIndex = event.rowIndex;
      axios.get('/api/admin/employees/' + emp).then(res => {
        console.log(res);
        this.info = res.data[0];
      }).catch(error => {
        console.error(error);
      });
      console.log(event);
    },
    //부서리스트 조회
    async departmentList() {
      await axios.get('/api/admin/departmentList').then(res => {
        console.log(res.data);
        this.departmentListAry = res.data;
      });
    },
    //값체크 validation
    checkValue() {
      if (this.info.emp_name == '' || this.info.department == '' || this.info.hire_date == '' || this.info.tel == '') {
        Swal.fire({
          title: '실패',
          text: '값을 다 입력해주세요',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
    },
    //등록
    async empSave() {
      console.log(this.info);
      //값체크 validation
      let validation = this.checkValue();
      if (validation == 1) {
        return;
      }
      if (this.info.emp_code != '') {
        Swal.fire({
          title: '등록 불가',
          text: '이미 사원번호가 등록된 건입니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
      console.log(this.rowData[this.prodIndex]);
      //등록시작
      const res = await axios.post('/api/admin/empSave', this.info).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '등록 성공',
            text: '정상적으로 등록이 완료되었습니다.',
            icon: 'success',
            confirmButtonText: '확인'
          });
        } else {
          Swal.fire({
            title: '등록 실패',
            text: '등록이 실패하였습니다..',
            icon: 'error',
            confirmButtonText: '확인'
          });
        }
      }).catch(error => {
        console.error(error);
        Swal.fire({
          title: '등록 실패',
          text: '알수 없는 오류가 발생하였습니다..',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      });
      await this.EmployeesData();
      this.info = {
        emp_code: "",
        emp_name: "",
        department: "",
        hire_date: "",
        tel: "",
        pwd: ""
      };
    },
    // 수정
    //값 다 넣었는지 체크
    async empModify() {
      let validation = this.checkValue();
      if (validation == 1) {
        return;
      }
      if (this.info.emp_code == '') {
        Swal.fire({
          title: '수정 불가',
          text: '사원번호가 필요합니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }

      //수정시작
      await axios.put('/api/admin/empModify', this.info).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '수정 완료',
            text: '수정이 완료되었습니다.',
            icon: 'success',
            confirmButtonText: '확인'
          });
          this.EmployeesData();
          this.info = {
            emp_code: "",
            emp_name: "",
            department: "",
            hire_date: "",
            tel: "",
            pwd: ""
          };
        } else {
          Swal.fire({
            title: '수정 실패',
            text: '수정을 실패하였습니다.',
            icon: 'error',
            confirmButtonText: '확인'
          });
          return;
        }
      }).catch(err => {
        console.error(err);
        Swal.fire({
          title: '수정 실패',
          text: '알수 없는 에러.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      });
      this.info = [{
        emp_code: "",
        emp_name: "",
        department: "",
        hire_date: "",
        tel: "",
        pwd: ""
      }];
    },
    //삭제
    async empDelete() {
      if (this.info.emp_code == '') {
        Swal.fire({
          title: '삭제 실패',
          text: '사원번호가 필요합니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
      await axios.delete('/api/admin/empDelete/' + this.info.emp_code).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '삭제 성공',
            text: '정상적으로 삭제되었습니다.',
            icon: 'success',
            confirmButtonText: '확인'
          });
          this.EmployeesData();
          this.info = {
            emp_code: "",
            emp_name: "",
            department: "",
            hire_date: "",
            tel: "",
            pwd: ""
          };
        } else {
          Swal.fire({
            title: '삭제 실패',
            text: '삭제를 실패하였습니다.',
            icon: 'error',
            confirmButtonText: '확인'
          });
          return;
        }
      }).catch(err => {
        console.error(err);
        Swal.fire({
          title: '삭제 실패',
          text: '알수 없는 에러.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      });
      this.info = [{
        emp_code: "",
        emp_name: "",
        department: "",
        hire_date: "",
        tel: "",
        pwd: ""
      }];
    },
    //초기화
    empReset() {
      this.info = [];
    }
  }
};